import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, Image, FlatList, ScrollView, Linking } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class Error extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  async componentDidMount() {
    const { route } = this.props;
    let session = await AsyncStorage.getItem('session');
    this.setState({
      session: session ? JSON.parse(session) : null,
    })
    let service = entry[route.params.e].service;
    fetch(service + 'api/vendor/rdp/get-transaction-result?transactionId=' + route?.params?.transaction_id)
      .then(response => response.json())
      .then(response => {
        console.log(response)
        this.setState({
          response: response
        })
      })
  }

  render() {
    const { navigation, route, } = this.props;
    const { session, response } = this.state;

    return (
      <View style={{ flex: 1, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}>
        {/* {
          session?.e &&
          <Image
            style={{ height: 40, width: 80, margin: 20 }}
            source={{
              uri: entry[session.e]?.logo
            }}
            resizeMode='contain'
          />
        } */}
        <Text style={{ fontFamily: 'demi', marginHorizontal: 20, textAlign: 'center' }}>{response?.order_id}</Text>
        <Text style={{ fontFamily: 'demi', marginHorizontal: 20, textAlign: 'center' }}>{response?.response_code} {response?.response_msg}</Text>
      </View>
    );
  }

}

const styles = StyleSheet.create({
});
