import { Component } from 'react';
import { AntDesign, Entypo, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, FlatList, Animated, TouchableOpacity, Text, Image, TextInput, Alert, ScrollView, Platform } from 'react-native';
import theme from '../assets/theme';
import moment from 'moment';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class MenuFolder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedFolderId: '',
    };

  }

  componentDidMount() {

  }

  handleViewableChanged({ viewableItems, changed }) {
    // console.log('child', viewableItems, changed)
    if (viewableItems.length === 0) return;
    const newSelectedFolderId = viewableItems?.[0].section.folderId;
    if (newSelectedFolderId !== this.state.selectedFolderId) {
      this.setState({
        selectedFolderId: newSelectedFolderId
      }, () => this.flatListRef.scrollToIndex({
        animated: true,
        index: this.props.folders.findIndex(el => newSelectedFolderId === el.folderId)
      }))
    }
  }

  doPressFolder(item) {
    const itemIndex = this.props.folders.findIndex(el => el.folderId === item.folderId)
    this.props.handlePressFolder(itemIndex)
    // problematic
    this.flatListRef.scrollToIndex({
      animated: true,
      index: itemIndex
    })
    this.setState({
      selectedFolderId: item.folderId,
    })
  }

  render() {
    const { } = this.props;
    const { } = this.state;

    return (
      <View style={{ height: 54, backgroundColor:theme.grey7,justifyContent:'center'}}>
        <View style={{ height: 50, backgroundColor: theme.grey1 }}>
          <FlatList
            ref={ref => this.flatListRef = ref}
            style={{ backgroundColor: theme.grey9, }}
            showsHorizontalScrollIndicator={false}
            horizontal
            data={this.props.folders}
            keyExtractor={item => item.recKey.toString()}
            renderItem={({ item }) =>
              <TouchableOpacity style={{ padding: 20, alignItems: 'center', justifyContent: 'center', backgroundColor: item.folderId === this.state.selectedFolderId ? theme.grey8 : theme.grey9 }}
                onPress={() => this.doPressFolder(item)}>
                <Text style={{ fontFamily: 'demi', fontSize: 16, }}>{item.name}</Text>
                {
                  (moment(item.startTime, 'HH:mm:ss').format('HH:mm') !== '00:00' || moment(item.endTime, 'HH:mm:ss').format('HH:mm') !== '23:59') &&
                  <Text style={{ fontFamily: 'regular', fontSize: 12, marginTop: -2 }}>{moment(item.startTime, 'HH:mm:ss').format('HH:mm')} ~ {moment(item.endTime, 'HH:mm:ss').format('HH:mm')}</Text>
                }
              </TouchableOpacity>
            }
          />
        </View>
      </View >
    );
  }

}

const styles = StyleSheet.create({

});
